import React from 'react';
import './editAlbum.less';
import FileInput from "../fileInput/FileInput";
import {CrossIcon, SaveIcon} from "../Icons";
import Card from "./Card";
import http from '../../HttpService';
import connect from "react-redux/es/connect/connect";
import {deleteFotoFromAlbum} from "../../actions/albumActions";
const routesModule = require('../../constants/routes');

@connect(store => ({
    
}), {
    deleteFotoFromAlbum
})
class EditAlbum extends React.Component {

    constructor(props) {
        super(props);
        this.state = {
            dragStarted: false,
            dragFrom: null,
            dragFromEl: null,
            dragObj: null,
            isTextOpen: false,
            album: null
        }
    }

    componentDidMount() {
        http.doGet(routesModule.routes.GET_ALBUM(this.props.match.params.id))
            .then(result => this.setState({pictures: result.images}));
    }

    openDetails() {
        this.setState({isTextOpen: true});
    }

    onCrossClick() {
        this.setState({isTextOpen: false});
    }

    deleteItem(picture) {
        this.props.deleteFotoFromAlbum(picture.id, this.state.album.pictures);
    }

    changeDragState(obj) {
        this.setState(obj);
    }

    render() {
        const {isTextOpen,album} = this.state;
        return album && (
            <div className='edit_container'>
                {this.state.album.pictures.sort((x,y) => x.order-y.order).map(p => <Card
                    key={p.id}
                    picture={p}
                    pictures={this.state.album.pictures}
                    openDetails={() => this.openDetails()}
                    deleteItem={() => this.deleteItem()}
                    replaceImage={(target) => this.replaceImage(target)}
                    isTextOpen={isTextOpen}
                    changeDragState={(obj) => this.changeDragState(obj)}
                    dragState={this.state}
                />)}
                <FileInput className='new_image' disabled={isTextOpen} label='Choose new foto'/>
                {isTextOpen &&
                <div className='new_text' ref={t => this.text = t}>
                    <input/>
                    <textarea/>
                    <SaveIcon className='save_icon'/>
                    <CrossIcon className='cross_icon' onClick={() => this.onCrossClick()}/>
                </div>}

            </div>
        );
    }
}

export default EditAlbum;



